(function () {
    'use strict';
    var angular = require('angular');
    require('./complete-ack.component.scss');
    require('./complete-ack.component.service');

    var thankYou = angular.module('app').component('completeAck', {
        template: require('./complete-ack.component.html'),
        controller: ['$rootScope', '$http','$state', '$anchorScroll', 'analyticService', 'appState', 'CONFIG','CompleteAckService',
            function ($rootScope, $http,$state, $anchorScroll, analyticService, appState, CONFIG,CompleteAckService) {
                var vm = this;

                vm.$onInit = function () {
                    var appData = appState.getApplicationData();
                    var contactData = appState.getContactDetails();
                    if(contactData && contactData.vcipType)
                    {
                        vm.vcipFlag=contactData.vcipType==='Y'?true:false;
                        vm.adobeID=contactData.adobeId;
                    }
                    if (appData && appData.primaryApplicant && appData.primaryApplicant.arn) {
                        vm.name = appData.primaryApplicant.dipDetails.nameOnCard;
                        vm.arn = appData.primaryApplicant.arn;
                        vm.refNumber = appData.primaryApplicant.dipDetails.dipArn;
                        vm.frnNumber = appData.primaryApplicant.frnNumber;
                        
                        // vm.documentURL = CONFIG.apiUrl + "/applicantdetails/getFinalDocument/" + appData.primaryApplicant.arn;
                        vm.documentURL = CONFIG.apiUrl + "/adobe/getSignedDocument/" + appData.primaryApplicant.arn;
                        vm.isStaffJourney = appState.isStaffJourney();

                        var status = $state.params.saveData.statusCode;
                        vm.autoUrl= $state.params.saveData.autoUrl;
                        //vm.arn=appData.primaryApplicant.arn;
                        //alert(vm.autoUrl);
                        switch (status) {
                            case 7000:
                                // STP journey with DIP approved and No Blacklist
                                vm.stpDip = true;
                                break;
                            case 7001:
                                // Non-STP Journey with DIP approved while going through Perfios and BlackList
                                // vm.nonStpDip = true;
                                vm.nonStp = true;
                                break;
                            case 7002:
                                // Non-STP journey: 5 days multiple case
                                vm.nonStp = true;
                                break;
                            default:
                                vm.nonStp = true;
                                break;
                        }
                        analyticService.pageList.applicationComplete.application_id = appData.primaryApplicant.token.substr(6);
                        analyticService.pageList.applicationComplete.application_timestamp = new Date().getTime();
                        analyticService.pageList.applicationComplete.application_time = "";
                        analyticService.pageList.applicationComplete.application_date = new Date().toDateString();
                        analyticService.pageList.applicationComplete.internal_reference_id = vm.adobeID;
                        //alert(vm.adobeID);
                        analyticService.trackPageLoad(analyticService.pageList.applicationComplete);
                        appState.removeApplicationData();
                    } else {
                        $state.go('app.journey.s1-cards');
                    }
                    $anchorScroll();
                };

                vm.routeautoUrl = function (){
                    var securityParams = {
                        robocheck: vm.robocheck ? vm.robocheck : ''
                    };
                    CompleteAckService.saveEkycData(securityParams,vm.arn);
                    console.log('auto===='+vm.autoUrl);
                    window.location.href = vm.autoUrl;
                };


                // vm.callEnd = function () {
                //     thankYouService.getSignDoc(vm.arn).then(function (result) {
                //         if (result.errorCode == 3 || result.errorCode == 4) {
                //             $state.go('app.journey.s1-cards');
                //         }
                //     }, function (err) {
                //         $anchorScroll();
                //     });
                // }

                vm.exit = function () {
                    window.location.href = "http://www.hsbc.co.in/";
                }

            }]
    });

    module.exports = thankYou.name;
})();



